"use strict";

import Namespace from './Namespace';
import JSObjectNamespace from './JSObjectNamespace';

/*
Javascript doesn't actually have dicts (or maps etc),
they are the same as JS objects. This class exists for
compatibility with parts of the API that require a dict.
*/
export default class DictNamespace extends Namespace {
    constructor(obj) {
        super();

        this.dict = obj;
        this.commonType = DictNamespace.getCommonType(obj);
        this.originalSize = Object.keys(obj).length;
        this.originalType = typeof obj;
    }

    callMethod(name, args) {
        switch (name) {
            case "type":
                return new JSObjectNamespace(this.originalType);
            case "size":
                return new JSObjectNamespace(Object.keys(this.dict).length);
            case "original_size":
                return new JSObjectNamespace(this.originalSize);
            default:
                return super.callMethod(name, args);
        }
    }

    static getCommonType(obj) {
        return "dict";
    }

    readKey(key) {
        return this.dict[key];
    }
}